import React,{ Component } from "react";
import './navStyle.css' ;
import {
    Navbar,
    Nav,
    NavDropdown
} from 'react-bootstrap';


class NavBar extends Component {
        render(){
        return (
            <Navbar collapseOnSelect expand="lg" bg="#fff" variant="light">
                <Navbar.Brand  href="/"><img width={this.props.width} height={this.props.height} src={this.props.logo} alt='CODEZILA'/></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav activeKey={this.props.location.pathname} className="mr-auto navbar-custom">
                        <Nav.Link exact={true} href="/" >Home</Nav.Link>
                        <Nav.Link href={process.env.PUBLIC_URL+"/codezila.org/learn"}>learning</Nav.Link>
                        <NavDropdown disabled title="Quiz" id="collasible-nav-dropdown" class="ddown-custom">
                            <NavDropdown.Item href="#dq">Daily Quiz</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Saturday Task</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.3">Sunday Contest</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="/#about" >About Us</Nav.Link>
                        <Nav.Link href="#contact">Contact Us</Nav.Link>
                    </Nav>
                    <Nav class="navbar-custom">
                    <Nav.Link disabled eventKey={2} href="#">
                        Login
                    </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>      
        );
        }
}

export default NavBar;